<template>
  <div>
    <a-row :gutter="16" class="bg">
      <a-col :span="3" class="left">
        <a-card :bordered="false">
          <div slot="cover" alt="example">
            <type-list @select="onSelect"></type-list>
          </div>
        </a-card>
      </a-col>
      <a-col :span="20" class="right">
        <component :is="activeItem" @ok="success" @done="onDone" ref="comp" v-if="refresh"/>
      </a-col>
    </a-row>
  </div>
</template>

<script>
import TypeList from '@/views/sys/parameter/TypeList'
import notification from 'ant-design-vue/es/notification'
export default {
  components: {
    TypeList,
    notification,
    WebClient:()=>import('@/views/sys/parameter/WebClient'),
    Standard:()=>import('@/views/sys/parameter/Standard'),
  },
  data() {
    return {
      activeItem: 'WebClient',
      data: null,
      refresh: false
    }
  },
  methods: { 
    onSelect(record){
      this.refresh = false
      window.setTimeout(()=>{
        this.refresh = true
        if(record.v3){
          this.activeItem = record.v3
        }
        else{
          this.activeItem = 'Standard'
        }
        this.data = record
      },1)
    },
    onDone(){
      this.$refs.comp.initData(this.data)
    },
    success() {
      this.$message.info(this.$t('param.tips3'))
    }
  }
}
</script>
<style lang="scss" scoped>
.bg{
  height: 100%;
  background: #ffffff;
  padding: 10px auto;
  .left {
    padding-right: 0!important;
    height: 100%;
  }
  .right{
    padding-left: 0!important;
    border-left: 1px solid #d9d9d9;
    height: 100%;
    margin: 20px auto;
  }
}
</style>