<template>
  <div>
    <a-list
      size="small"
      :split="false"
      :dataSource="dataSource"
      class="li"
    >
      <a-list-item slot="renderItem" slot-scope="item,index" type="inner" :bordered="false" class="list">
        <div @click="onClick(item,index)" :class="key==index ? 'active': ''"> 
          <span class="title">
            <a-icon :type="item.v1" />
            {{ ($t('sparm.' + item.tableType +'_'+ item.code) != 'sparm.' + item.tableType +'_'+ item.code)?$t('sparm.' + item.tableType +'_'+ item.code):item.description }}
          </span>
        </div>
      </a-list-item>
    </a-list>
  </div>
</template>
<script>
import { axios } from '@/utils/request'
export default {
  data () {
    return {
      dataSource: [],
      key: 0,
    }
  },
  mounted(){
    this.loadData()
  },
  methods: {
    loadData() {
      const that = this
      axios({
        url: '/sys/parameter/search-type',
        method: 'get',
        params: {}
      }).then(data=>{     
        that.dataSource = data.result
        if(that.dataSource.length > 0){
          that.onClick(that.dataSource[0], 0)
        }
      })
    },
    onClick(item,index){
      const that = this
      that.key = index
      this.$emit('select',item)
    }
  },
}
</script>
<style lang="scss" scoped>
.li {
  margin-top: 15px;
 .list {
   width: 100%;
   text-align: right;
   font-size: 17px;
   div {
     width: 100%;
     .title {
       margin: 6px 10px 6px;
       display: inline-block;
     }
   }
  .active {
    background: #e6ffff;
    border-right: 3px solid #1890ff;
    color: #1890ff;
  }
 }
}
</style>